@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;600;700&family=Montserrat:wght@300;400;500;600;700&display=swap);
*,::before,::after{margin:0;padding:0;box-sizing:border-box;outline:none}body{font-family:"Cabin", sans-serif}ul{margin:0;padding:0;list-style:none}a{text-decoration:none}.modal .btn-primary{background-color:#115147;border-color:#115147}.modal .btn-secondary{opacity:0.8;background-color:#ff0000;border-color:#ff0000}

#navbar{overflow:hidden;background-color:#115147;position:fixed;width:100%;z-index:1}

#allProducts{overflow:hidden;padding:9rem 0}#allProducts h2{margin-bottom:3rem}#allProducts .card{position:relative;height:380px;margin:0.8rem}#allProducts .card:hover .cartBtn{opacity:1;visibility:visible}#allProducts .card:hover img{transform:scale(1.1);-webkit-transform:scale(1.1);-moz-transform:scale(1.1);-ms-transform:scale(1.1);-o-transform:scale(1.1)}#allProducts .card img{height:300px;transition:0.5s;-webkit-transition:0.5s;-moz-transition:0.5s;-ms-transition:0.5s;-o-transition:0.5s}#allProducts .cartBtn{color:#115147;position:absolute;top:0;right:0;font-size:2rem;opacity:0;visibility:hidden;transition:all 0.4s linear;-webkit-transition:all 0.4s linear;-moz-transition:all 0.4s linear;-ms-transition:all 0.4s linear;-o-transition:all 0.4s linear}

#details{overflow:hidden;padding:9rem 0;padding:130px 0}#details .mainDetails{padding:1rem}#details .mainDetails img{margin:0 auto;height:400px;width:100%}#details .text-red-200{margin:0 1rem;font-size:1.5rem;letter-spacing:2px;color:#115147;transition:0.3s;-webkit-transition:0.3s;-moz-transition:0.3s;-ms-transition:0.3s;-o-transition:0.3s}#details .text-red-200:hover{font-size:2rem}#details .text-red-200:first-child{margin:0}

